vm._v("— На это может уйти вся оставшаяся жизнь, — сказала я в надежде, что это практическое замечание поможет Кларе более трезво взглянуть на вещи.")]),_c('p',[_vm._v("— Может уйти, — согласилась она. — Но уверяю тебя, Тайша, что, позанимавшись вспоминанием, ты ничего не потеряешь, но можешь приобрести нечто превосходящее все твои ожидания.")]),_c('p',[_vm._v("Я сделала несколько глубоких вдохов, покачивая головой из стороны в сторону, стараясь делать это так, как она мне показала. Этим я хотела успокоить ее и дать ей понять, что слушала ее внимательно.")]),_c('p',[_vm._v("Косо улыбнувшись, она предупредила меня, что вспоминание — это не случайное досужее занятие.")]),_c('p',[_vm._v("Практикуя вспоминание, старайся ощутить длинные натянутые волокна, которые выходят из солнечного сплетения, — объяснила она. — Затем согласуй движение головы с движениями этих едва заметных волокон. Они являются проводниками, по которым утраченная когда-то энергия вновь возвращается к тебе. Для того, чтобы стать сильной и вернуть себе целостность, ты должна высвободить всю свою энергию, которая была в течение жизни уловлена миром, и вновь вобрать ее в себя.")]),_c('p',[_vm._v("Она уверяла меня, что во время вспоминания мы распространяем эти длинные энергетические волокна по пространству-времени, соединяясь ими с теми людьми, местами и событиями, с которыми мы имели дело в прошлом. В Результате мы оказываемся в состоянии вернуться в каждый прошлый момент своей жизни и действовать в нем так, как если бы мы еще раз реально проживали его.")]),_c('p',[_vm._v("Эта перспектива заставила меня вздрогнуть. Хотя интеллектуально я была и заинтригована тем, что мне рассказала Клара, у меня не было никакого желания возвращаться в свое отвратительное прошлое, даже если речь шла об обычном путешествии в воображении. Ведь я гордилась даже тем, что смогла как-то выпутаться из той невыносимой обстановки, в которой была вынуждена жить с самого раннего детства. Я вовсе не собиралась снова посещать свое прошлое и воскрешать в памяти все те моменты, которые мне больше всего хотелось забыть. Однако Клара выглядела такой серьезной и сосредоточенной, объясняя мне технику вспоминания, что я решила на некоторое время отложить свои сомнения в сторону и выслушать то, что она мне излагала.")]),_c('p',[_vm._v("Я спросила ее, имеет ли значение порядок, в котором человек вспоминает свое прошлое. Она ответила, что важнее всего научиться ярко проживать в воображении события прошлого, припоминая как можно больше подробностей, связанных с ними, и очищая их при этом с помощью выметающего дыхания, которое освобождает уловленную в них энергию.")])])],1)}
